.gallery-weather {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(181px, 1fr));
  grid-auto-rows: 220px;
  justify-content: center;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.info {
  text-align: center;
}

.search-form {
  background-color: rgba(17, 240, 192, .298);
  border-radius: 10px;
  justify-content: center;
  gap: 10px;
  padding: 20px;
  display: flex;
}

input {
  width: 300px;
  height: 20px;
  border-radius: 5px;
}

button {
  width: 80px;
  border-radius: 7px;
}

h1, .weather-card {
  text-align: center;
}

